@font-face {
  font-family: "TheJamsil5Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2")
    format("woff2");
  font-weight: 100;
  font-style: normal;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: "TheJamsil5Bold";
  height: 100%;
  width: 100%;
}

nav {
  /* background-color: #e69a06; */
}

.nav-container {
  width: 100%;
  height: 100%;
  padding: 2rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.nav-title {
  font-size: 3rem;
}

.nav-safe {
  width: 50px;
  font-size: 1.5rem;
  border: 0;
  cursor: pointer;
  font-family: inherit;
  background-color: white;
}

.nav-safe:hover {
  background-color: rgb(202, 199, 199);
}

.category-title {
  font-size: 3.5rem;
}

.category-list {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  z-index: 100;
  border-top: 1px;
  background-color: white;
}

.category-item {
  width: 25%;
  height: 5rem;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 1.6rem;
}

.category-item:hover {
  color: gray;
  cursor: pointer;
}

.inner {
  padding: 0 1.5rem;
  width: 100%;    /* 1024 이상 잘림 방지*/
  height: 100%;   /* 확장 */
}

@media all and (min-width: 1024px) {
  .inner {
    /*max-width: 1024px;*/
    margin: 0 auto;
  }
}

