.search-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  margin-top: 0;
  padding-bottom: 0;
}

.search-container input {
  padding: 5px 10px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.search-container button {
  padding: 5px 10px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.search-container button:hover {
  background-color: #555;
}


#map {
  position: relative; /* 이를 통해 map 내부의 자식 요소들에게 절대 위치를 지정할 수 있게 됩니다. */
}

#myRoundButton {
  position: absolute; /* 버튼의 위치를 절대 위치로 지정 */
  top: 10px; /* 버튼을 지도의 상단에서 10px 떨어진 곳에 위치 */
  left: 10px; /* 버튼을 지도의 왼쪽에서 10px 떨어진 곳에 위치 */
  z-index: 10; /* 다른 지도 요소들 위에 버튼이 보이게 하기 위해 z-index 값을 크게 설정 */
  border-radius: 50%; /* 버튼을 원형으로 만들기 위해 */
  padding: 10px 15px; /* 버튼 내부의 패딩 값 지정 */
}

.searchInput {
  width: 50px;
  height: 100%;
  border: 0px;
  outline: none;
}

.ol-full-screen.ol-unselectable.ol-control {
  visibility: hidden;
}

.switch_wrapper{
  height: 50px;
  width: 50px;
  align-items: center;
  margin: 50px auto;
  bottom: 0;
}

.chkbox{
  position: absolute;
  appearance: none;
}

.switch_label{
  position: relative;
  width: 50px;
  height: 25px;
  background-color: white;
  display: inline-block;
  border: 2px solid rgb(245, 200, 193);
  border-radius: 18px;
  transition: 0.3s;
}

.switch_label:hover{
  background-color: rgb(228, 225, 225);
}

.btn{
  position: absolute;
  width: 15px;
  height: 15px;
  top: 3px;
  left: 2px;
  display: inline-block;
  border-radius: 15px;
  background-color: rgb(245, 200, 193);
  transition: 0.2s;
}

/* 체크 */

.chkbox:checked+.switch_label{
  background: rgb(247, 117, 57);
  border: 2px solid rgb(247, 117, 57);
  
}

.chkbox:checked+.switch_label:hover{
  background-color: rgb(255, 132, 101);
}

.chkbox:checked+.switch_label .btn{
  left: 29px;
  background-color: white;
  box-shadow: 1px black;
}

.ol-popup {
  position: absolute;
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border: 1px solid #cccccc;
  bottom: 12px;
  left: -50px;
  min-width: 280px;
  z-index: 0;  
}
.ol-popup:after, .ol-popup:before {
  top: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.ol-popup:after {
  border-top-color: white;
  border-width: 10px;
  left: 48px;
  margin-left: -10px;
}
.ol-popup:before {
  border-top-color: #cccccc;
  border-width: 11px;
  left: 48px;
  margin-left: -11px;
}